export  function DropHead(ctx,sA,eA,c) {
    let dhe=null;
   return  dhe ={1:ctx.beginPath(),//glava
    2:ctx.arc(200,c,50, sA,eA),
    3:ctx.lineWidth = 10,
    4:ctx.fillStyle = "transparent",
    5:ctx.stroke(),
   }
  }

  export function PlayAudio(myAudio){ 
    myAudio.play(); 
    console.log('MYAUDIO')
  } 
  export function PauseAudio(myAudio){ 
     myAudio.pause(); 
     console.log('MYAUDIO')
   }